import { useState, useCallback } from "react";
import { useMeasure, useUpdateEffect } from "react-use";
import { useSpring } from "@react-spring/web";
import { createContainer } from "unstated-next";

// export const animationStageDuration = 400;

interface InitialCollapse {
  open?: boolean;
  defaultOpen?: boolean;
}

function useCollapseStage(
  initialState: InitialCollapse = { open: undefined, defaultOpen: true }
) {
  const [_open, setOpen] = useState(() => {
    if (typeof initialState.open === "boolean") return initialState.open;
    return !!initialState.defaultOpen;
  });
  const _setOpen = useCallback(
    (value: React.SetStateAction<boolean>) => setOpen(value),
    []
  );
  const [headerRef, { height: headerHeight }] = useMeasure();
  const [contentRef, { height: contentHeight }] = useMeasure();
  const initialed = !!headerHeight && !!contentHeight;

  // animations
  const [wrapperAnimation, collapseAnimAPI] = useSpring(
    () => ({ to: { height: 0 } }),
    []
  );
  // init animation value
  useUpdateEffect(() => {
    if (initialed) {
      collapseAnimAPI.set({
        height: _open ? headerHeight + contentHeight : headerHeight,
      });
    }
  }, [initialed]);
  const toggle = useCallback(() => setOpen((v) => !v), []);
  const onOpen = useCallback(() => setOpen(true), []);
  const onClose = useCallback(() => setOpen(false), []);
  return {
    isOpen: _open,
    setOpen: _setOpen,
    headerRef,
    contentRef,
    collapseAnimAPI,
    wrapperAnimation,
    toggle,
    onOpen,
    onClose,
    initialed,
    headerHeight,
    contentHeight,
  } as const;
}

const container = createContainer(useCollapseStage);

export const CollapseProvider = container.Provider;

export const useCollapse = container.useContainer;

export default container;
