import { memo, forwardRef, useMemo, useEffect } from "react";
import styled from "styled-components";
import { animated } from "@react-spring/web";
import { CollapseProvider, useCollapse } from "./useCollapse";

export interface CollapseProps
  extends Omit<
    React.HTMLAttributes<HTMLDivElement>,
    "title" | "style" | "onClick"
  > {
  /**
   * @default: undefined
   * @description if provided, controlled by open
   */
  open?: boolean;
  /**
   * @default: true
   * @description if open provided, controlled by open
   */
  defaultOpen?: boolean;
  /** @description title component  */
  title: React.ReactNode;
  /** @description collapse content */
  children: React.ReactNode;
  /** @description extra content will be put into wrapper */
  extraNode?: React.ReactNode;
  /** @description styles for header wrapper */
  headerStyles?: React.CSSProperties;
  /** @description styles for content wrapper */
  contentStyles?: React.CSSProperties;
  /** @default false */
  disableHeaderToggle?: boolean;
}

export const Collapse = forwardRef<HTMLDivElement, CollapseProps>(
  ({ defaultOpen = true, ...props }, ref) => {
    return (
      <CollapseProvider
        initialState={{ open: props.open, defaultOpen: defaultOpen }}
      >
        <CollapseContent {...props} defaultOpen={defaultOpen} ref={ref} />
      </CollapseProvider>
    );
  }
);
export const CollapseContent = forwardRef<HTMLDivElement, CollapseProps>(
  (props, ref) => {
    const {
      title,
      headerStyles,
      contentStyles,
      children,
      extraNode,
      disableHeaderToggle,
      open: propsOpen,
      defaultOpen,
      ...rest
    } = props;
    const {
      isOpen,
      collapseAnimAPI,
      wrapperAnimation,
      toggle,
      headerRef,
      contentRef,
      initialed,
      headerHeight,
      contentHeight,
    } = useCollapse();
    const open = typeof propsOpen === "boolean" ? propsOpen : isOpen;
    // styles
    const contentStyle = useMemo(() => {
      if (!headerHeight || !contentHeight)
        return {
          opacity: 0,
          pointerEvents: "none",
        } satisfies React.CSSProperties;
      const s: React.CSSProperties = contentStyles || {};
      return { ...s, top: headerHeight };
    }, [contentStyles, headerHeight, contentHeight]);
    const style = initialed ? wrapperAnimation : undefined;
    // update by content change & open status
    useEffect(() => {
      if (!initialed) return;
      const toHeight = open ? headerHeight + contentHeight : headerHeight;
      collapseAnimAPI.start({ to: { height: toHeight } });
    }, [initialed, headerHeight, contentHeight, collapseAnimAPI, open]);
    return (
      <Wrapper {...rest} ref={ref} style={style}>
        {/* absolute content */}
        <ContentWrapper
          ref={(element) => element && contentRef(element)}
          style={contentStyle}
        >
          {children}
        </ContentWrapper>
        {extraNode}
        {/* header */}
        <HeaderWrapper
          ref={(element) => element && headerRef(element)}
          style={headerStyles}
          onClick={disableHeaderToggle ? undefined : toggle}
        >
          {title}
        </HeaderWrapper>
      </Wrapper>
    );
  }
);

export default memo(Collapse);

export const Wrapper = styled(animated.div)`
  position: relative;
  overflow: hidden;
`;

export const HeaderWrapper = styled.div`
  ${(p) => p.theme.flexCSS.row}
  align-items: center;
  position: relative;
`;

export const ContentWrapper = styled(animated.div)`
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
`;
